*{
  box-sizing:border-box;
  font-family: "Rubik",sans-serif;
  color:#393c41;
  margin:0;
  padding: 0;
}
a{
  text-decoration: none;
}
body{
  width: 100%;
  overflow-x: hidden;
}
@keyframes animateDown {
  0%,20%,50%,80%,100%{
    transform: translateY(0);
  }
  40%{
    transform: translateY(5px);
  }
  60%{
    transform:translateY(3px);
  }
}
.hamburger {
  cursor: pointer;
  position: relative;
  padding: 20px;
  background:#ed0a0a;
}
.hamburger span{
  width: 45px;
  height: 2px;
  
  position:relative;
  transition: .4s ease;
  margin-bottom: 5px;
}